import React from "react";
import { Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Var from '../variable';
import '../styles/main.css'

export default function About({ darkState }) {
  const useStyles = makeStyles((theme) => ({
    title: {
      color: darkState ? "#ffffff" : '#343a40',
    }
  }));
  const classes = useStyles();
  return (
    <div className="mt-32">
      <div className={[classes.title, "text-5xl font-bold"].join(' ')}>{Var.name} <span className="text-primary">{Var.surname}</span></div>
      <div className="sub-heading font-bold mt-2 mb-8">{Var.state} · {Var.city} · <a title="email" href="mailto:name@email.com">{Var.email}</a></div>
      <Typography paragraph>
        Hi! I'm {Var.name} – a software architect with a strong passion for the web application development.<br />
        I'm part of many communities in order to talk with the other developers about technologies, programming languages and frameworks.
      </Typography>
    </div>
  );
}
